.body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.className {
  background-color:#282c34;
}

#idName {
  background-color: #282c34;
}

div {
  background-color: #282c34;
}

.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.button-container img {
  width: 170px;
  height: 170px;
  margin: 30px;
}

.button-container img:hover {
  transform: scale(1.2);
  transition: transform 0.3s ease-in-out;
}

.full-width {
  width: auto; 
  height: 100%; 
  object-fit: cover; 
  position: absolute;
  top: 0;
  left: 0;
}

.section {
  position: relative; 
  overflow: hidden; 
}

.section img[alt="Title"] {
  width: 80%;
  height: auto;
}

.button-container-center {
  display: flex;
  justify-content: center;
  width: 60%;
  margin-top: 80px;
}

.button-container-center img {
  margin: 0 40px;
  width: 200px; 
  height: 200px; 
}

.button-container-space-between {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 50px;
}

.button-container-space-between img {
  transition: transform 0.3s;
}

.button-container-space-between img:hover {
  transform: scale(1.2);
}

.img-button img { 
  width: 50%;
}

.section img[alt="Profile"] {
  width: 50%; 
  height: auto;
}
